
import React from 'react';
import { PanelHeader, Group, Tooltip,  Touch, List, Alert, View, Panel, Div, Button, ScreenSpinner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css'
import axios from 'axios';
const images = {
	girl:"https://www.big-ben.ru/sites/default/files/reviews/mo.png",
	boy: "https://lh3.googleusercontent.com/XtyURW0mKNnKu_6TzQ5_WpuKF4A7M1oFV6p828eVEWIvTZPtZz2gq5sNM78jpNPMMRmZ"
};
const layerStyle = {
	backgroundColor: 'rgba(248, 247, 216, 0.1)',
	position: 'absolute',
	top: 0,
	left: 0,
	width: '100%',
	height: '100%',
	//transition: 'background-color .3s'
}
// eslint-disable-next-line
const userTwoStyle = {
	padding: 0,
	backgroundColor: '#f8f8f8',
}
const touchStyle = {
	position: 'absolute',
	backgroundColor: '#f8f8f8',
	backgroundPosition: 'center',
	backgroundSize: '90%',
	boxShadow: '0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24)'

}
const UserPicStyle = {
	backgroundSize: 'contain',
	backgroundRepeat: 'no-repeat',
	overflow: 'hidden',
	backgroundPosition: 'center',
	margin: 0,
	padding: 0
}
const divContainerInterests = {
	display:'flex',
	flexWrap: 'wrap',
	justifyContent:'space-around',
	alignContent:'right'
}
class Cards extends React.Component {

	constructor(props) {

		super(props);

		this.state = {
			id: props.id,
			userInterests: props.userInterests,
			addPair: props.addPair,
			user: props.user,
			users: [],
			rot: 0,
			loading: false,
			moveBack: 0.1,
			shiftX: 0,
			shiftY: 0,
			debugInfo: '',
			limitOffset: 3,
			userPicBackgroundColor: '',
			sizeX: 390,
			sizeY: 900,
			halfX: 390 / 10,
			minchange: 2,
			popout: null
		}
		this.startX = 0;
		this.startY = 0;
		this.renderInterests = this.renderInterests.bind(this);

		this.onMove = this.onMove.bind(this);
		this.refreshUsers = this.refreshUsers.bind(this);
		this.onEnd = this.onEnd.bind(this);
		this.getUsers = this.getUsers.bind(this);
	}
	componentDidMount() {
		this.setState({ sizeX: window.innerWidth, sizeY: window.innerHeight });
		this.getUsers();
	}

	onMove(e) {
		let shiftX = this.startX + e.shiftX;
		let rotation = shiftX / this.state.halfX;
		this.setState({
			rot: rotation,
			moveBack: 0.1,
			shiftX: shiftX
		});
	}
	getUsers(e) {
		var t = this;
		t.setState({ popout: <ScreenSpinner /> });
		axios.get(`https://salert.info/user/GetUsers/${this.state.user.id}`
		).then((x) => {
			t.setState({ popout: null, debugInfo: `done-${JSON.stringify(x.data)}`, users: x.data });
		}).catch(function (x) {
			t.setState({ popout: null, debugInfo: 'Ошибка ' + JSON.stringify(x) });
		});
	}
	onEnd(e) {
		var deb = this.state.debugInfo;
		var users = this.state.users;
		if (Math.abs(this.state.rot) < this.state.limitOffset) {
			this.setState({ rot: 0, moveBack: 0.5, shiftX: 0, shiftY: 0 });
			return;
		}
		if (this.agree || this.disAgree) {
			if (this.agree) {

				deb += `+${this.state.users[0].first_name}`;
			}
			else {
				deb += `-${this.state.users[0].first_name}`;
			}
			var model = {
				currentUserId: this.state.user.id,
				choicedUserId: users[0].id,
				choiced: this.agree
			};
			var t = this;
			var rotation = t.state.rot > 0 ? 15 : -15;
			this.setState({ userPicBackgroundColor: '', rot: rotation, debugInfo: deb, moveBack: 0.1, shiftX: 0, shiftY: 0 });

			axios.post('https://salert.info/user/SetChoice', model).then((x) => {
				if (x.data === true) {
					var newPair = users[0];
					t.state.addPair(newPair);
				}


				users.splice(0, 1);

				t.setState({ rot: 0, users: users, moveBack: 0 });
				if (users.length === 0) {
					t.getUsers();
				}
				//this.showAlert("",JSON.stringify(users),"");

			}).catch(function (x) {
				t.showAlert("Ошибка", JSON.stringify(x), "");

			});
		}
		else {
			var rotation = this.state.rot > 0 ? 15 : -15;
			setTimeout(() => {
				users = users.splice(0, 1);
				this.setState({ rot: 0, users: users, moveBack: 0 });
			}, 100)
		}
	}
	refreshUsers(e) {
		this.getUsers();
	}

	getCircleRef(el) { this.circleRef = el };

	get agree() {
		const { shiftX, limitOffset } = this.state;
		return Math.abs(shiftX) > limitOffset && shiftX > 0;
	}
	get disAgree() {
		const { shiftX, limitOffset } = this.state;
		return Math.abs(shiftX) > limitOffset && shiftX < 0;
	}
	showAlert(title, text, closeText) {
		this.setState({
			popout:
				<Alert
					actions={[{
						title: closeText,
						autoclose: true,
					}]}
					onClose={(e) => { this.setState({ popout: null }) }}
				>
					<h2>{title}</h2>
					<p>{text}</p>
				</Alert>
		});
	}
	getSexPreference(user) {
		var ages = user.ageFromPreference?` от ${user.ageFromPreference}`:'';
		ages += user.ageToPreference?` до ${user.ageToPreference}`:'';

		var preference = user.sexPreference == 0 ? `хоть кого ${ages}` : user.sexPreference == 1 ? `женщиу ${ages}` : `мужчину ${ages}`;
		return preference;
	}
	getSex(user) {
		var sex = user.sex == 0 ? "не указан" : user.sex == 1 ? "женский" : "мужской";
		return sex;
	}
	getReason(user) {
		var reason;
		if (user.searchReason == undefined) {
			return "неизвестно вообще чего";
		}
		switch (user.searchReason) {
			case 0: reason = "общения"; break;
			case 1: reason = "дружбы"; break;
			case 2: reason = "любви"; break;
			case 3: reason = "секса"; break;
			default: reason = "неизвестно вообще чего"; break;
		}
		return reason;
	}
	birthDateToAge(birthDate) {
		birthDate = new Date(birthDate);
		var now = new Date(), age = now.getFullYear() - birthDate.getFullYear();



		return now.setFullYear(1972) < birthDate.setFullYear(1972) ? age - 1 : age;

	}

	getYo(user) {
		var date  = new Date(user.bdate + "Z");
		var years = this.birthDateToAge(date);
		return `${date.getFullYear()}, ${years} лет`;
	}
	renderInterests(interests) {
		var listItem;// eslint-disable-next-line
		listItem = [];
		var userInterests = this.state.userInterests;
		var count = 0;
		if (interests != undefined) {
			interests.forEach((e) => {
				var isCool = userInterests.find((x) => { return x.id === e.id });				
				listItem.push(<Button level={isCool?"primary":"secondary"} component="span" style={{marginTop:5, marginRight:3}}>{e.title}</Button>);
			});
		}
		else {
			listItem = <b>Нет интересов</b>
		}
		return <Div style={{ lineHeight: '2.5', ...divContainerInterests }}> {listItem}</Div>
			
		
	};


	render() { // eslint-disable-next-line
		const { id, popout, users, sizeX, sizeY, moveBack, debugInfo, userPicBackgroundColor, rot } = this.state;
		const user = users[0];
		return (
			<View id={id} activePanel={id} popout={popout}>
				<Panel id={id} style={{ overflow: 'hidden' }}>
					<PanelHeader>{rot} New</PanelHeader>
					<Group>
						{users.length !== 0 && users[0] !== undefined &&
							<Touch
								onMove={this.onMove}
								onEnd={this.onEnd}

								style={{ ...touchStyle, transformOrigin: `center ${sizeY * 3}px`, width: sizeX, height: sizeY, transition: `transform ${moveBack}s ease-out`, transform: `rotate(${rot}deg)` }}
							>
								<Div style={{ height: '100%', padding: 0 }}>
									<div style={{ ...layerStyle, backgroundColor: userPicBackgroundColor, }}></div>
									<Div>
									<h1 style={{ textAlign: 'center' }}>
										<strong>{user.first_name} {user.last_name}</strong>
									</h1>
									<h5>{this.getYo(user)}</h5>
									<h5>Пол {this.getSex(user)}, ищет {this.getSexPreference(user)} для {this.getReason(user)}</h5>
									</Div>


									<Div style={{ ...UserPicStyle, width: sizeX, height: sizeY / 4, backgroundImage: `url(${user.sex ==1?images.girl:images.boy})` }}></Div>

									{this.renderInterests(user.interests)}

								</Div>
							</Touch>
						}

						{/*users.length !== 0 && users[1] !== undefined &&
						<Div style={{ ...userTwoStyle, height: sizeY }}>
							<Div style={{ ...UserPicStyle, width: sizeX, height: sizeY / 2, backgroundImage: `url(${image})` }}></Div>
							<h1 style={{ textAlign: 'center' }}>
								<strong>{users[1].first_name} {users[1].last_name}</strong>
							</h1>
							{this.renderInterests(users[1].interests)}
						</Div>
					*/}
						{users.length === 0 &&
							<Group>
								<h1>Пока что больше нету :( Можешь попытаться обновить, но сейчас это не особо работает бро </h1>
								<Button onClick={this.refreshUsers}>Сбросить</Button>
								<Div>{debugInfo}</Div>
							</Group>

						}
					</Group>

				</Panel>
			</View>
		)
	}
}

export default Cards;