import React from 'react';
// eslint-disable-next-line
import { Panel, Group, Button, Div, CellButton, Separator, FormLayoutGroup, RangeSlider, Radio, Alert, ListItem, Checkbox, Cell, FormLayout, List, HeaderButton, ModalPageHeader, ModalPage, View, ModalRoot, Avatar, PanelHeader, Input, Select, ScreenSpinner } from '@vkontakte/vkui';
import Icon16Add from '@vkontakte/icons/dist/16/add';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';
import axios from 'axios';
import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from '@vkontakte/vkui/dist/lib/platform';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import '@vkontakte/vkui/dist/vkui.css'
const USER_INTERESTS_MODAL = 'user_interests';
const USER_MAXIMUM_INTERESTS = 10;



class Profile extends React.Component {

	constructor(props) {

		super(props);

		this.state = {
			syncInterests:props.syncInterests,
			userInterests: props.userInterests,
			activeModal: null,
			openInterestsModal: props.openInterestsModal,
			saveUserSettings:props.saveUserSettings,
			id: props.id,
			user: props.user,
			interests: [],
			debugInfo: "Тут дебаг",
			deNorm: "Ну тесты",
			popout: null,
			userSettings: props.userSettings
		}
		this.renderUserInterests = this.renderUserInterests.bind(this);
		this.setActiveModal = this.setActiveModal.bind(this);
		this.getInterests = this.getInterests.bind(this);
		this.renderModalInterests = this.renderModalInterests.bind(this);
	}
	componentDidMount() {
		this.initInterests();
	}
	renderUserInterests() {
		var listItem = [];
		if (this.state.userInterests !== undefined && this.state.userInterests.length > 0) {
			this.state.userInterests.forEach((e) => {
				listItem.push(<Button style={{ marginRight: 5 }} onClick={() => this.deleteInterestFromCurrentUser(e.id)} after={<Icon16Cancel />}>{e.title}</Button>);
			});
		}
		else {
			listItem = <b>Нет интересов</b>
		}
		return <Div
			style={{ padding: 10, lineHeight: '2.5' }}> {listItem}
		</Div>
	}
	
	showAlert(title, text, closeText) {
		this.setState({
			popout:
				<Alert
					actions={[{
						title: closeText,
						autoclose: true,
					}]}
					onClose={(e) => { this.setState({ popout: null }) }}
				>
					<h2>{title}</h2>
					<p>{text}</p>
				</Alert>
		});
	}
	showAddInterestsModal() {
		if (this.state.userInterests.length < USER_MAXIMUM_INTERESTS) {

			this.setActiveModal(USER_INTERESTS_MODAL);
		}
		else {
			this.showAlert("Ошибка", `Нельзя добавить более ${USER_MAXIMUM_INTERESTS} интересов`, "Закрыть");
		}
	}
	setActiveModal(e) {
		this.setState({ activeModal: e })
	}
	addInterestToCurrentUser(id) {
		if (this.state.userInterests.length < USER_MAXIMUM_INTERESTS) {
			var t = this;
			this.setState({ popout: <ScreenSpinner /> });
			var model = {
				userId: this.state.user.id,
				interestId: id
			}
			axios.post(`https://salert.info/interests/AddInterest`, model
			).then((x) => {
				t.setState({ popout: null, debugInfo: `done-${JSON.stringify(x.data)}`, userInterests: x.data });
				t.syncInterests(t.state.userInterests);
			}).catch(function (x) {
				t.setState({ popout: null, debugInfo: 'Ошибка ' + JSON.stringify(x) });
			});
		}
		else {
			this.setActiveModal(null);
			this.showAlert("Ошибка", "Нельзя добавить более 10 интересов", "Закрыть");
		}
	}

	deleteInterestFromCurrentUser(id) {
		var t = this;
		var model = {
			userId: this.state.user.id,
			interestId: id
		}
		this.setState({ popout: <ScreenSpinner /> });
		axios.post(`https://salert.info/interests/DeleteInterest`, model
		).then((x) => {
			t.setState({ popout: null, debugInfo: `done-${JSON.stringify(x.data)}`, userInterests: x.data });
			t.syncInterests(t.state.userInterests);
		}).catch(function (x) {
			t.setState({ popout: null, debugInfo: 'Ошибка ' + JSON.stringify(x) });			
		});
	}
	renderModalInterests() {
		if (this.state.interests == null) {
			return "Нет результатов"
		}
		else {
			var listItem = [];
			this.state.interests.forEach((e) => {
				if (this.state.userInterests && this.state.userInterests.find((x) => { return x.id === e.id }) !== undefined) {
					listItem.push(<Button style={{ marginRight: 5 }} onClick={() => this.deleteInterestFromCurrentUser(e.id)} after={<Icon16Cancel />}>{e.title}</Button>);
				}

				else {
					listItem.push(<Button level="outline" style={{ marginRight: 5 }} onClick={() => this.addInterestToCurrentUser(e.id)} after={<Icon16Add />}>{e.title}</Button>);
				}
			});
			return <Div style={{ padding: 15, lineHeight: '2.5' }}>{listItem}</Div>
		}
	}
	initInterests(e) {
		var t = this;
		axios.get(`https://salert.info/interests/GetTopInterests`,
		).then((x) => {
			t.setState({ debugInfo: JSON.stringify(x.data), interests: x.data });
		}).catch(function (x) {
			t.setState({ debugInfo: 'Ошибка ' + JSON.stringify(x) });
		});


	}
	getInterests(e) {
		if (e.target.value.length < 2) {
			this.setState({ interests: null })
			return;
		}
		else {
			var t = this;
			axios.get(`https://salert.info/interests/SearchFromQuery?str=${e.target.value}`,
			).then((x) => {
				t.setState({ debugInfo: JSON.stringify(x.data), interests: x.data });
			}).catch(function (x) {
				t.setState({ debugInfo: 'Ошибка ' + JSON.stringify(x) });
			});
		}

	}
	render() { // eslint-disable-next-line
		const { id, user, popout, saveUserSettings, activeModal,originalUserSettings, userSettings, userInterests, interests, deNorm } = this.state;
		const modal1 =
			<ModalRoot activeModal={activeModal}>
				<ModalPage popout={popout}
					id={USER_INTERESTS_MODAL}
					settlingHeight={100}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <HeaderButton onClick={() => { this.setActiveModal(null) }}><Icon16Cancel /></HeaderButton>}
							right={<HeaderButton onClick={() => { this.setActiveModal(null) }}>{IS_PLATFORM_IOS ? 'Готово' : <Icon24Done />}</HeaderButton>}
						>Фильтры</ModalPageHeader>
					}>

					<this.renderModalInterests></this.renderModalInterests>
					<FormLayout>
						<FormLayoutGroup top="Поиск" onInput={(e) => { this.getInterests(e) }}>
							<Input placeholder="Начните вводить" />
						</FormLayoutGroup>
					</FormLayout>
				</ModalPage>
			</ModalRoot>

		return (
			<View id={id} popout={popout} modal={modal1} activePanel={id}>
				<Panel id={id} >
					<PanelHeader>
						Профиль
				</PanelHeader>
					{user &&
						<Group title="Общие данные">
							<Cell
								size="m"
								before={<Avatar src={user.photo_max_orig} size={80} />}>
								{user.first_name} {user.last_name}

							</Cell>
						</Group>
					}
					{user &&
						<Group title={`Интересы (${!userInterests ? 0 : userInterests.length}\\${USER_MAXIMUM_INTERESTS})`}>
							{userInterests &&
								<this.renderUserInterests></this.renderUserInterests>
							}
							<Separator style={{ margin: '12px 0' }} />
							<CellButton onClick={() => { this.showAddInterestsModal() }} style={{ paddingBottom: 10 }} before={<Icon16Add />}> Добавить интересы</CellButton>

						</Group>
					}
					{user &&
						<Group title={`Настройки поиска`}>
							<FormLayout>
								<RangeSlider
									top={`Возраст (от ${userSettings.ageFromPreference == null ? "любого" : userSettings.ageFromPreference.toString()} до ${userSettings.ageToPreference == null ? "любого" : userSettings.ageToPreference.toString()})`}
									min={12}
									max={115}
									step={1}
									defaultValue={[userSettings.ageFromPreference == null ? 0 : userSettings.ageFromPreference, userSettings.ageToPreference == null ? 115 : userSettings.ageToPreference]}
									onChange={(e) => { 
										userSettings.ageFromPreference = (e[0] == 12 ? null : e[0]);
										userSettings.ageToPreference = (e[1] == 115 ? null : e[1]);
										this.setState({ userSettings: userSettings}) ;
									}}


								/>
							</FormLayout>
							<FormLayout>
								<Select
									top="Показывать мне..."
									onChange={(e) => {										
										userSettings.sexPreference = e.target.value;
										this.setState({ userSettings: userSettings });
									}}
									value={userSettings.sexPreference}
									name="sexPreference"
								>
									<option value={0}>Всех</option>
									<option value={1}>Женщин</option>
									<option value={2}>Мужчин</option>
								</Select>
							</FormLayout>
							<FormLayout>
								<Select
									top="Для..."
									onChange={(e) => {
										userSettings.searchReason = e.target.value;
										this.setState({ userSettings: userSettings })
									}}
									value={userSettings.searchReason}
									name="searchReason"
								>
									<option value={0}>Общения</option>
									<option value={1}>Дружбы</option>
									<option value={2}>Любви</option>
								</Select>
								<Div style={{ display:'flex' }}>
									<Button size="l" onClick={(e)=>{this.setState({userSettings:originalUserSettings})}} stretched level="destructive">Сбросить</Button>
									<Button size="l" onClick={()=>{var res = saveUserSettings(userSettings); this.showAlert(JSON.stringify(res))}} stretched style={{ marginRight: 8 }}>Сохранить</Button>
								</Div>							
							</FormLayout>
						</Group>
					}
				</Panel>
			</View>
		)
	}
}

export default Profile;